// 常用class
.header{
  background-color: $primarycolor;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.logo{
  a{
    display: block;
    width: 80px;
    height: 49px;
    background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week3-4/logo-white.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

  }
}
.nav{
    li{
      font-size: 20px;
      line-height: 30px;
    }
    a{
      display: block;
      padding: 25px;
      margin-left: 14px;
      color:#FFFFFF;
      text-decoration: none;
      &:hover{
        opacity: 70%;
      }
  }
}
.banner{
  background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/home-header.png?raw=true);
  h1{
  color: #650300;
  font-size: 64px;
  line-height: 96px;
  font-weight: bold;
  text-align: right;
  span{
    font-size: 36px;
    line-height: 54px;
    font-weight: normal;
  }
  }
}
.container{
  max-width: 1200px;
  margin:0 auto;
  padding: 0px 12px;
  &-s{
    max-width: 636px;
    margin: 0 auto;
  }
}
.buybtn{
  background-color: $primarycolor;
  color:#FFFFFF;
  font-size: 20px;
  line-height: 30px;
  font-weight: bold;
  text-decoration: none;
  padding: 6px 12px;
  &:hover{
    background-color: #650300;
  }
}
.feature{
  background-color: $secondarycolor;
}
.title{
  color: #650300;
  font-size: 48px;
  line-height: 72px;
  font-weight: bold;
  text-align: center;
  &-white{
    color:white;
  }
  &-ita{
    color: #AA0601;
    font-style: italic;
    font-weight: 900;
  }
}
.icon{
  color: #650300;
  li{
    max-width: 23%;
    h3{
      font-size: 24px;
      line-height: 36px;
      font-weight: bold;
      text-align: center;
      span{
        display: block;
        font-size: 84px;
        font-weight: bold;
      }
    }
    p{
      color: #000;
    }
  }
  }
  .classic{
    li{
      max-width: 30%;
    }
  }
  .section-4{
    background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/index-bg.png?raw=true);
  }
  .cobrand{
    h3{
      background-color: #fff;
    }
  }
  .recommend{
    li{
      max-width: 23%;
      border-radius: 4px;
      border: 1px solid #DEE2E6;
      box-shadow: 0px 2px 6px #00000029;
      span{
        color: #707070;
        font-size: 14px;
        line-height: 21px;
      }
    }
  }
  .contact{
    background-color: $secondarycolor;
    p{
      font-size: 20px;
      line-height: 30px;
    }
  }
  .form{
    &-type{
      border: 1px solid #000;
      padding-left: 12px;
      padding-top: 6px;
      padding-bottom: 6px;
      span{
        opacity: 20%;
      }
    }
  }
  .footer{
    background-color: $primarycolor;
    color: #FFF;
    a{
      color: #FFFFFF;
      text-decoration: none;
    } 
    &-contact{
      border-bottom: 1px solid #FFF;
      li{
        font-size: 36px;
        line-height: 54px;
        span{
          font-size: 48px;
        }
      }
    }
    &-logo{
      img{
        width: 48px;
        height: 48px;
      }
    }
    }
  